/* 
//
//
// Containers
//
// 
*/
/* Flex Row */
.toolbar-container {
    width: 100%;
    height: 700px;
    background-color: var(--gray);
    position: absolute;
    top: 100vh;
    left: 0;
    z-index: 700;
    display: flex;
}
/* First column */
.tabs-container {
    width: 18%;
    padding: 0 3%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
/* Second column */
.demo-container {
    width: 70%;
    padding: 3%;
    height: fit-content;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
/* First component First column */
.script-toggle-container {
    margin-top: 120px;
}
/* Second component First column */
.demo-toggle-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-top: 20px;

}
/* First component Second column */
.toolbar-title-container {
    width: 100%;
    height: 130px;
    display: flex;
    justify-content: center;
    margin: 20px 0 0 0;
}
/* Second component Second column */
.script-demo-container {
    margin: 0 auto;
    width: 100%;
    background-color: var(--light-white);
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: 2px 2px 20px 5px var(--shadow);
}
/* 
//
//
// Tabs to select script type
// First component First column
//
// 
*/
.script-toggle-label {

}
.script-toggle-wrap {
    width: 100%;
    display: flex;
    justify-content: space-between;
}
.tooltab {
    width: 100px;
    height: 40px;
    border: none;
    margin: 5px 10px;
    border-radius: 30px;
    box-shadow: 2px 2px 20px 5px var(--shadow);
}
.tooltab-inactive {
    background-color: var(--light-white);
    color: var(--dark-green);
    border: 1px solid var(--dark-green);
}
.tooltab-active {

    background-color: var(--dark-green);
    color: var(--light-white);
    border: 3px double var(--mint);
}
.tooltab-inactive:hover {
    border: 3px double var(--dark-green);
    cursor: pointer;
}
/* 
//
//
// Tabs to select demo
// Second component First column
//
// 
*/
.demo-toggle-label {

}
.script-tab-active {
    height: 80px;
    width: 100%;
    border: none;
    border-right: 5px solid var(--mint);
    margin: 5px 0;
    background-color: var(--dark-green);
    color: var(--light-white);
    box-shadow: 2px 2px 20px 5px var(--shadow);
}
.script-tab-inactive {
    height: 80px;
    width: 100%;
    border: none;
    border-right: 5px solid var(--light-white);
    margin: 10px 0;
    background-color: var(--light-white);
    color: var(--dark-green);
    box-shadow: 2px 2px 20px 5px var(--shadow);
}
.script-tab-inactive:hover {
    border-right: 5px solid var(--mint);
    cursor: pointer;
}
/* 
//
//
// Toolbar title
// First component Second column
//
// 
*/
.toolbar-title-wrap {
    width: 420px;
    height: 73px;
    border-bottom: 1px solid var(--dark-green);
}
.toolbar-title {
    margin: 10px 0 0 0;
    font-size: 50px;
    color: var(--dark-green);
    text-decoration: none;
}
.toolbar-subtitle {
    color: var(--dark-green);
    font-size: 20px;
    margin: 10px 0 0 0;
}
/* 
//
//
// Script demo
// Second component Second column
//
// 
*/
.script-title {
    font-size: 48px;
    margin: 15px 0 10px 0;
}
.highlight-title {
    font-size: 54px;
    color: var(--mint);
}
.script-image {
    height: 300px;
    max-width: 95%;
}
.script-image {
    width: 90%;
}
.script-description {
    font-size: 20px;
}