

@media screen and (max-width: 900px) {
    /* 
    //
    //
    // Containers
    //
    // 
    */
    /* First column */
    .tabs-container {
        width: 215px;
        padding: 0 3%;
    }
    /* Second column */
    .demo-container {
        width: 70%;
        padding: 3%;
    }
    /* First component First column */
    .script-toggle-container {
        margin-top: 120px;
        padding: 0 30px;
    }
    /* Second component First column */
    .demo-toggle-container {
        width: 100%;
        display: flex;
        flex-direction: column;
        margin-top: 20px;

    }
    /* First component Second column */
    .toolbar-title-container {
        width: 100%;
        height: 130px;
        display: flex;
        justify-content: center;
        margin: 20px 0 0 0;
    }
    /* Second component Second column */
    .script-demo-container {
        margin: 0 auto;
        width: 100%;
        background-color: var(--light-white);
        display: flex;
        flex-direction: column;
        align-items: center;
        box-shadow: 2px 2px 20px 5px var(--shadow);
    }
    /* 
    //
    //
    // Tabs to select script type
    // First component First column
    //
    // 
    */
    .script-toggle-wrap {
        flex-direction: column;
        display: block;
    }
    .tooltab {
        width: 100px;
        height: 40px;
        border: none;
        margin: 5px 10px;
        border-radius: 30px;
        box-shadow: 2px 2px 20px 5px var(--shadow);
    }
    .tooltab-inactive {
        background-color: var(--light-white);
        color: var(--dark-green);
        border: 1px solid var(--dark-green);
    }
    .tooltab-active {

        background-color: var(--dark-green);
        color: var(--light-white);
        border: 3px double var(--mint);
    }
}

