.header {
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    top: 0;
    z-index: 999;
    transition: .3s;
}
.small {
    height: 60px;
    background: linear-gradient(0.25turn, var(--forrest-green), var(--dark-green), var(--forrest-green), var(--forrest-green));
    border-bottom: none;
}
.nav {
    margin: 0 60px 0 0;
}
.app-title {
    font-size: 26px;
    color: var(--light-white);
    text-decoration: none;
    margin: 0 0 0 100px;
}
.nav-link {
    margin: 0 15px 0 15px;
    font-weight: 600;
}
.nav-link:hover {
    color: var(--mint);
}