.video {
    width: 95%;
    margin: 0 15px 0 50px;
    position: absolute;
    left: 0;
    top: 80px;
}
.hero-container {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: flex-start;
}
.hero-content {
    width: 100%;
    height: 100%;
    z-index: 500;
    padding: 12% 0 0 15%;
    background: linear-gradient(0.25turn, #000000CC, #00000066, #00000000);
    text-align: left;
    font-size: 60px;
    color: white;
}
.welcome-header {
    margin: 0;
}
.highlight-text {
    color: var(--mint);
}
.welcome-subheader {
    font-size: 16px;
    color: white;
}
