/* Custom Variables */
* {
  --mint: #44AF69;
  --shadow: #D9D5CF;
  --light-white: #FFF8F0;
  --dark-green: #4E6E58;
  --forrest-green: #233329;
  --gray: #F5EFE7;
}
html {
  background-color: black;
}

body {
  margin: 0;
}

.App {
  text-align: center;
}
.font-title {
  font-family: "CourgetteRegular";
}
.outer-container {
  width: 100%;
  min-height: 100vh;
  height: fit-content;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.content-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}